<template>
  <div>
    <div class="pic">
      <img src="../../assets/icon/003.jpg" alt srcset style="height:300px;width:100%" />
    </div>
    <div class="container">
      <div class="sidebar">
        <ul class="main">
          <router-link tag="span" to="/introduce/office">
            <li
              :style="{background:this.$route.path == '/introduce/office'?'#4d4d4d':'',color:this.$route.path == '/introduce/office'?'#fff':''}"
            >律师事务所</li>
          </router-link>
          <router-link tag="span" to="/introduce/leader">
            <li
              :style="{background:this.$route.path == '/introduce/leader'?'#4d4d4d':'',color:this.$route.path == '/introduce/leader'?'#fff':''}"
            >团队介绍</li>
          </router-link>
          <router-link to="/introduce/Collection/0" tag="span">
            <li
              :style="{background:this.$route.path == '/introduce/Collection/0'?'#4d4d4d':'',color:this.$route.path == '/introduce/Collection/0'?'#fff':''}"
              @click.self="handleseClick"
            >
              <span style="display:inline-block;margin-left:20px" @click.self="handleseClick">团队文集</span>
              <span>
                <img
                  v-show="isShow"
                  @click.self="handleseClick"
                  style="width:16px;"
                  src="../.././assets/icon/减号.png"
                  alt
                  srcset
                />
                <img
                  v-show="!isShow"
                  @click.self="handleseClick"
                  style="width:16px"
                  src="../.././assets/icon/加号.png"
                  alt
                  srcset
                />
              </span>
            </li>
          </router-link>
          <ul v-show="isShow" class="second">
            <router-link
              tag="li"
              to="/introduce/collection/company"
              :style="{background:this.$route.path == '/introduce/collection/company'?'#4d4d4d':'',color:this.$route.path == '/introduce/collection/company'?'#fff':''}"
            >公司法律</router-link>
            <router-link
              tag="li"
              to="/introduce/Collection/labour"
              :style="{background:this.$route.path == '/introduce/Collection/labour'?'#4d4d4d':'',color:this.$route.path == '/introduce/Collection/labour'?'#fff':''}"
            >劳动法律</router-link>
            <router-link
              tag="li"
              to="/introduce/Collection/formlawyer"
              :style="{background:this.$route.path == '/introduce/Collection/formlawyer'?'#4d4d4d':'',color:this.$route.path == '/introduce/Collection/formlawyer'?'#fff':''}"
            >形式法律</router-link>
          </ul>
        </ul>
      </div>
      <div class="content">
        <router-view />
      </div>
    </div>
    <Float></Float>
    <div class="maps" v-show="this.$route.path === '/introduce/office'">
      <!-- <img class="map" src="../../assets/icon/微信图片_20190722185019.png" alt="" srcset=""> -->
      <Map></Map>
      <!-- <img class="tel" src="../../assets/icon/contact_bj.jpg" alt=""> -->
      <div class="tel">
        <div class="address1">
          <p>公司地址:青岛市市北区黑龙江南路2号万科中心A栋5楼</p>
          <p>联系电话:13375557517</p>
          <p>18561616688</p>
          <!-- <p>联系人:xxxxx</p> -->
          <p>QQ:XXXXXX</p>
          <P>微信:XXXXXXX</P>
          <p>公众号:XXXXXXX</p>
          <p>邮编:266000</p>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import Footer from "../../common/Footer/footer";
import Float from "../../common/Float/Float";
import Map from "../../common/Map/Map";
export default {
  components: {
    Footer,
    Float,
    Map
  },
  data() {
    return {
      isShow: false,
      teamisShow: false,
      init: 0
    };
  },
  methods: {
    handleseClick() {
      this.isShow = !this.isShow;
    },
    handleTeam() {
      this.teamisShow = !this.teamisShow;
    },
    countShow() {
      this.isShow = true;
    },
    dele() {
      this.isShow = false;
    }
  }
};
</script>

<style scoped>
.container {
  width: 1200px;
  margin: 20px auto;
  display: flex;
  padding-top: 20px;
}
.ishowOne {
  background: red;
}
.chat {
  height: 70px;
  width: 70px;
  background: #b1976b;
  text-align: center;
  color: #fff;
  padding: 5px 2px;
  margin-bottom: 5px;
  font-size: 16px;
}
.main {
  padding-left: 0px;
  position: relative;
}
.timeShow {
  font-size: 18px;
  color: #fff;
  text-align: center;
  line-height: 70px;
  background: blue;
  height: 70px;
  width: 250px;
  display: inline-block;
  background: #b1976b;
  position: absolute;
  right: 74px;
  top: 85px;
  padding: 5px 2px;
}
.tag {
  position: fixed;
  right: 10px;
  bottom: 10px;
}
.lead {
  height: 70px;
  width: 70px;
  text-align: center;
  background: #666666;
  padding: 5px 2px;
}
/* }
.showOne{
    background-color:red;
    color: blue
} */
/* .map{
    height: 310px;
    width: 700px;
} */
.address1 {
  width: 365px;
  height: 285px;
  text-align: center;
  padding-top: 8px;
  margin: 10px auto;
  background: rgba(12, 92, 110, 0.5);
}
.address1 p {
  font-size: 16px;
  color: #fff;
  margin: 10px auto;
  font-family: "Microsoft YaHei";
}
.tel {
  flex: 1;
  height: 310px;
  background-image: url(../../assets/icon/contact_bj.jpg);
  background-repeat: none;
  background-size: 100%;
}
.maps {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  margin-bottom: 20px;
  background-color: red;
}
.sidebar {
  height: 500px;
  font-size: 18px;
  width: 280px;
}
.content {
  flex: 1;
  margin-left: 50px;
  padding-top: 20px;
}

.pic {
  width: 100%;
  height: 300px;
  background: #eee;
  margin: 0 auto;
}
.sidebar li {
  list-style: none;
  padding: 10px;
  background: #eee;
  text-align: center;
  cursor: pointer;
}
li:hover {
  background: #4d4d4d;
  color: #fff;
}
.second {
  padding: 0px;
  margin: 0;
  width: 280px;
  color: black;
  position: relative;
  margin-bottom: 40px;
  font-size: 16px;
}
</style>


