<template>
    <div>
       <div class="pic">
           <img src="../../assets/icon/003.jpg" alt="" srcset="" style="height:300px;width:100%">
       </div>
        <div class="container">
        <div class="sidebar">
                <!-- <li style="background:#B1976B;color:black; font-family:'Microsoft YaHei';font-size:18px;">
                公司法律服务</li>    -->
                <ul class="seul">
                    <router-link 
                    to="/server/top" 
                    tag="li" 
                    class="de" 
                    :style="{background:this.$route.path == '/server/top'?'#4d4d4d':'',color:this.$route.path == '/server/top'?'#fff':''}">公司法律服务</router-link>
                    <router-link 
                    to="/server/equityeightesign/1" 
                    tag="li" 
                    class="de" 
                    :style="{background:this.$route.path == '/server/equityeightesign/1'?'#4d4d4d':'',color:this.$route.path == '/server/equityeightesign/1'?'#fff':''}">股权设计</router-link>
                    <router-link 
                    to="/server/equityincentive" 
                    tag="li" 
                    class="de"
                    :style="{background:this.$route.path == '/server/equityincentive'?'#4d4d4d':'',color:this.$route.path == '/server/equityincentive'?'#fff':''}"
                    >股权激励</router-link>
                    <router-link 
                    to="/server/equityallocationtable" 
                    tag="li" 
                    class="de"
                    :style="{background:this.$route.path == '/server/equityallocationtable'?'#4d4d4d':'',color:this.$route.path == '/server/equityallocationtable'?'#fff':''}"
                    >报价配置表</router-link>
                </ul>
            
        </div>
        <div class="content">
            <router-view></router-view>
        </div>
        <Float></Float>

    </div>
    <Footer>
    </Footer>
    </div>
</template>

<script>
import Footer from '../../common/Footer/footer'
import Float from '../../common/Float/Float'
export default {
    components:{
        Footer,
        Float
    },
    data(){
        return {
            isShow:false
        }
    },
    methods:{
        handleClick(){
            this.isShow=!this.isShow
        }
    }
}
</script>

<style scoped>
.container {
    width: 1200px;
    margin: 20px auto;
    display: flex;
    padding-top:20px;
}
.pic{
     width: 100%;
    height: 300px;
    background: #eee;
    margin: 0 auto;
}
.sidebar{
    width: 280px;
}
.sidebar li{
    list-style: none;
    padding: 10px;
    background-color: #eee;
    text-align: center;
    cursor: pointer;
}
.sidebar li:hover{
   background: #4d4d4d;
   color: #fff
}
.seul{
    left:70px
}
.seul li{
    width: 260px
}
/* .fili {
    font-size: 18px;
    font-family: "Microsoft YaHei"
} */
.de{
    font-size: 18px;
    font-family: "Microsoft YaHei"
}
/* ul li{
    cursor: pointer;
    background: #eee;
    list-style: none
} */
.content{
    flex: 1;
    margin-left:50px 
}
ul{
    padding-left: 0px
}
</style>



