<template>
  <div id="app1">
    <div id="allmap" ref="allmap"></div>
   <!-- <router-view></router-view> -->
  </div>
</template>
 
<script>
export default {
  name: 'App',
  methods:{
    map(){
      let map =new BMap.Map(this.$refs.allmap); // 创建Map实例
      map.centerAndZoom(new BMap.Point(120.3990933079,36.1169317103), 20);// 初始化地图,设置中心点坐标和地图级别
      map.addControl(new BMap.MapTypeControl({//添加地图类型控件
        mapTypes:[
          BMAP_NORMAL_MAP,
          BMAP_HYBRID_MAP
        ]}));
      const new_point = new BMap.Point(120.3990933079,36.1169317103);
      const marker = new BMap.Marker(new_point);
      map.addOverlay(marker); 
      map.setCurrentCity("青岛");// 设置地图显示的城市 此项是必须设置的
      map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
      
    }
  },
  mounted(){
    this.map()
 
  }

}
</script>
 
<style>
#app1 {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  width: 700px;
  height: 310px;

}
#allmap{
  height: 310px;
  width: 700px;
  overflow: hidden;
 
}
</style>



