<template>
    <div id="container1">
        <span  class="name">{{items.lawyerName}}</span>
        <span class="profess">{{items.lawyerLevel}}</span>
        <img  style="width:250px;height:170px" :src="items.lawyerImg" alt="" />
        <p class="profess" v-html="items.introduction" ></p>
        <!-- <p class="profess">一、法学研究成绩</p> -->
    </div>

</template>

<script>
export default {
    data() {
        return {
            items:{}
            
        }
    },
    mounted(){
        this.$http.get("/lawyerList").then((res)=>{
            let data = res.data;
        //    console.log(this.$route.path.split('/').pop());  
        for(var i=0;i<data.length;i++){
            if(this.$route.params.id==data[i].lawyerId){
                this.items=data[i];
                // console.log(data[i])
            }
        } 
        //     this.items=data[this.$route.params.id];
        //    console.log([this.$route.params.id])
            
        })
    }
}
</script>
<style scoped>
.name{
    font-family: "Microsoft YaHei";
    font-size: 28px;
}
.profess{
    font-size: 14px;
}
img{
    display: block;
    margin: 20px 0;
    width: 250px;
    height: 170px;
}
</style>



