<template>
    <div class="office" v-html="items">

    </div>
</template>

<script>
export default {
    data() {
        return {
            items:{},
            host:this.GLOBAL.host
           
        }
    },
    
    mounted(){
       this.$http.get("/agency").then((res)=>{
           let data = res.data;    
        //  console.log(data[0].agencyDetail)
           this.items= data
           
       })
       
    }
}
</script>

<style scoped>
.map .tel{
    float: left;
}
.title{
    font-size: 28px;
    margin-bottom:28px;
    margin-top: 0px;
    font-family: "Microsoft YaHei";
    color: #666666;
}
</style>



