<template>
    <div>
        <div class="div1" v-show="isShow" style="width:100%;height:1000px"></div>
        <div id="container" style="overflow:hidden">
            <ul>
                    <li v-for="(item,index) in items" :key="index" >
                    <router-link tag="div" :to="`/introduce/leader/`+item.lawyerId">
                    <img :src="item.lawyerImg" alt="" style="width:250px;height:170px;cursor:pointer" />
                    <div class="desc">
                        <span class="name1">{{ item.lawyerName }}</span>
                        <span class="profess">{{ item.lawyerLevel }}</span>
                        <span class="intro" v-html="item.introduction.substr(0,80)+'...'"></span>
                        
                    </div>
                    </router-link>
                </li>
            </ul>
           
        </div>
         <!-- <div  style="text-align:center;font-size:18px;color:666666">{{ loading }}</div> -->
    </div>
</template>

<script>
export default {
    name:"Leader",
    data(){
        return {
            items:[],
            isShow:true,
            // dataLength:'',
            // loading:'下拉刷新'
        }
    },
    methods: {
        
    },
    created(){
        if(!this.items[0]){
            this.isShow=false;
        }
    },
    mounted() {
        // window.addEventListener('scroll',this.handleScroll)
        
        this.$http.get('/lawyerList').then((res)=>{
            let data =res.data;
            this.items = data;
            // this.dataLength = data.length;  
            // this.items=data.slice(0,4);
           
            
        })
    },
    // filters:{
    //     introFilter(value) {
    //         if(value.length>100){
    //             return value.slice(0,80)+'...'
    //         }else {
    //             return value
    //         }
    //     }
    // },
    methods: {
    //     handleScroll(){
    //         if(this.dataLength == this.items.length){
    //             this.loading = '到底了...'
    //         }else{
    //             this.$http.get('/lawyerList').then((res)=>{
    //                 let data= res.data
    //                 if(document.body.scrollHeight-document.documentElement.scrollTop <= document.documentElement.clientHeight+1){
    //                     let res1 = data.slice(this.items.length,this.items.length+4)
    //                     this.items=this.items.concat(res1)
                        
    //             }
    //         }) 
    //     }
    // }
}
   
}
</script>

<style scoped>
#container{
    margin-top: -15px
}
ul {
    width: 770px;
    list-style: none;
    padding: 0px;
}
ul li{
    display: flex;
    margin: 15px auto;
    clear: both;
}
ul li .desc{
    padding:20px;
    float: right;
    display: inline-block;
    width: 480px;
    height: 130px;
    cursor: pointer;
    background-color:#eee;
}
ul img{
   flex: 1;
}
ul li .desc .name1{
    display: inline-block;
    font-family: "Microsoft YaHei";
    font-size: 28px;
}
ul li .desc .profess{
    /* margin: -25px; */
    font-size: 14px;
}
ul li .desc .intro{
    display: block;
    font-family: "Microsoft YaHei";
    font-size: 14px;
    line-height: 25px;
    margin-top:13px; 
    padding: 5px;
}
</style>


