<template>
    <div>
        <div id="container">
            <div id="title">在线留言</div>
            <div id="word">有什么问题请留言,我们会在第一时间联系您并解答</div>
            <div id="form">
                <input v-model="customerName" class="name common" style="text-align:left" type="text" placeholder="姓名" />
                <input v-model="customerTel" class="tel common" type="text" placeholder="联系方式"  />
                <textarea v-model="caseDetail" class="msg common" placeholder="留言"/>
                <button id="sub"  @click="sub" type="submit">提交</button>
            </div>
            

        </div>
    </div>
</template>

<script>
export default {
  name: 'Online',
  data() {
      return {
          customerName:'',
          customerTel:'',
          caseDetail:''
      }
  },
  methods: {
      sub(){
          if(this.customerName!=''&&this.customerTel!=''&&this.caseDetail!=''){
           this.$http.post('/insertOneMsg',{
               customerName:this.customerName,
               customerTel:this.customerTel,
               caseDetail:this.caseDetail
           }).then((res)=>{
               let data = res.data;
               if(data){
                   alert('提交成功！')
               }
               else{
                   alert('提交失败！')
               }
               this.customerName='';
               this.customerTel='';
               this.caseDetail='';
        })
      }
      else{
          alert('输入不能为空！')
      }
      }
  },
  
}
</script>

<style scoped>
#container{
    padding-top: 10px;
    width:100%;
    height:400px;
    background-image: url(../../assets/icon/contactus_bj.jpg);
    background-size: 100%;
    font-family:"Microsoft YaHei";
    text-align:center;
    
}
#title{
    font-size:22px;
}
#word{
    font-size: 16px;
    margin-top: 12px;
}

#form{
    width:600px;
    height: 230px;
    margin: 20px auto;
}

.name{
    display: block;
    width: 600px;
    height: 30px;
    font-size: 16px;
    border: 1px  solid #b9bbd0;
    margin-bottom: 10px;
    padding-left: 20px;
    background-color:rgba(255,255,255,0.6);

}
.tel{
    display: block;
    width: 600px;
    height: 30px;
    border: 1px  solid #b9bbd0;
    margin-bottom: 10px;
    padding-left: 20px;
    background-color:rgba(255,255,255,0.6);
}
.msg{
    display: block;
    font-family:"Microsoft YaHei";
    width: 600px;
    height:130px;
    border: 1px  solid #b9bbd0;
    padding-left: 20px;
    background-color:rgba(255,255,255,0.6);
}
#sub{
    float: left;
    margin-top: 10px;
    font-family:"Microsoft YaHei";
    font-size: 16px;
    width: 156px;
    height: 40px;
    border:1px solid #fff;
    background-color: #fec401;
    opacity:0.7;
    cursor: pointer;
    
}
</style>



