import Footer from '../../common/Footer/footer'
import Lawser from './components/Lawser'
import Penaldef from './components/Penaldef'
import Crimedel from './components/Crimedel'
import Curdefense from './components/Currentdefense'
import Float from '../.././common/Float/Float'
export default {
    data () {
       return {
            menuId: 0,
            isShow:false
       }
    },
    methods: {
        handleClick (id) {
            this.$router.push('/penallaw/lawser/'+id)
            this.menuId = id
        },
         handleseClick(){
            this.isShow = !this.isShow
        }
    },
