<template>
    <div class="tag" v-show="chat">
                <div style="display:inline-block">
                    <div 
                    class="chat"
                    @mouseover="handleQrClick"
                    @mouseleave="handleQrLeave"
                    >
                    <img 
                src="../../assets/icon/rg.png" 
                style="position:absolute;bottom:150px;right:70px;padding-right:10px" 
                srcset=""
                v-show="isQrShow"
                >
                    <img style="width:100%;height:100%" src="../.././assets/icon/rBQBHVvFfy-Ebtn2AAAAAKFhTlQ402.png" alt="">
                </div>
                <div 
                class="chat"
                @mouseover="letTimeShow"
                @mouseleave="letTimeNotShow"
                >
                <div
                class="timeShow"
                v-show="timeShow"
                @mouseleave="handleLetTimeIshow"
                >
                周一到周五9:00-17:30
                </div>
                    <img style="width:100%;height:100%" src="../.././assets/icon/rBQBHFvFfw6EOkI_AAAAADLuZss878.png" alt="">
                </div>
                <div class="lead" @click="handleMove">
                    <img src="../.././assets/icon/rBQBG1vFf1mEeyw_AAAAAI8PZ3E434.png" alt="">
                </div>
                </div>
            </div>
</template>

<script>
export default {
    data(){
        return {
            chat:false,
            isQrShow:false,
            timeShow:false
        }
    },
     mounted() {
        window.addEventListener('scroll', this.handleScroll)
    },
    methods:{
        handleseClick(){
            this.isShow = !this.isShow
        },
        handleLetTimeIshow() {
            timeShow:false
        },
        handleQrLeave() {
            this.isQrShow = false
        },
        handleScroll() {
            // console.log(document.body.scroconsllHeight)//全文的高
            // console.log(document.documentElement.scrollTop)//全文高
            // console.log(document.body.clientHeight)
            // if(document.body.scrollHeight-document.body.clientHeight==){
            //     alert('yep')
            // }
            if(document.documentElement.scrollTop>200){
                this.chat=true
            }else{
                this.chat=false
                this.isQrShow = false
            }
        },
        letTimeShow() {
            this.timeShow = true,
            this.isQrShow=false
        },
        letTimeNotShow() {
            this.timeShow = false
        },
        handleQrClick() {
            this.isQrShow = true
        },
        handleMove(){
            document.documentElement.scrollTop=0
        },
   
}
}
</script>

<style scoped>
.chat{
    height:70px;
    width:70px;
    background:#b1976b;
    text-align:center;
    color:#fff;
    padding: 5px 2px;
    margin-bottom: 5px;
    font-size: 16px
}
.timeShow{
    font-size:16px;
    font-family:'微软雅黑';
    color:#fff;
    text-align:center;
    line-height:70px;
    background:blue;
    height:70px;
    width:300px;
    display:inline-block;
    background:#b1976b;
    position: absolute;
    right: 70px;
    top: 85px;
    padding: 5px 2px
}
.tag{
    position: fixed;
    right: 10px;
    bottom: 115px;
}
.lead{
    height:70px;
    width:70px;
    text-align:center;
    background: #666666;
    padding: 5px 2px;
}
</style>



