<template>
    <div class="all">
        <img class="picUrl" :src="items.picUrl" />
        <div class="title">{{items.title}}</div>
        <div class="desc">{{items.desc}}</div>
        <div class="process">办案是否顺利：{{items.process}}</div>
        <div class="result">{{items.result}}</div>
        <div class="lessions">{{items.lessions}}</div>
       
    </div>
</template>
<script>
export default {
    data() {
        return {
            items:{}
        }
    },
    mounted() {
        this.$http.get('/getOneCaseById?id='+this.$route.params.id).then((res)=>{
            let data = res.data;
            this.items = data;
            console.log(this.items)
            
        })
    },
}
</script>
<style scoped>
.all{
    color: #666666;
    margin-top: 15px;
}
.title{
    font-size: 26px;
    font-family: "Microsoft YaHei";
    margin-bottom: 20px;
}
.desc{
    margin-bottom: 20px;
}
.process{
    margin: 20px 0;
}
.lessions{
    margin: 20px 0;
}
</style>


