<template>
    <swiper :options='swiperOption' style="margin-top:-4px">
        <swiper-slide v-for="(item,index) in items" :key='index'><img style="width:100% ;height:100%" :src='item.imgPath'  alt="" srcset=""></swiper-slide>
        <!-- <swiper-slide><img style="width:100% ;height:100%" src="http://192.168.43.33:8081/images/banner-pic/04.jpg" alt="" srcset=""></swiper-slide>
        <swiper-slide><img style="width:100% ;height:100%" src="http://192.168.43.33:8081/images/banner-pic/04.jpg" alt="" srcset=""></swiper-slide>
        <swiper-slide><img style="width:100% ;height:100%" src="http://192.168.43.33:8081/images/banner-pic/04.jpg" alt="" srcset=""></swiper-slide> -->
        <!-- Optional controls -->
        <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
</template>

<script>

export default {
     data () {
      return {
        items:[],
        swiperOption: {
            autoplay : true,
            loop: true,
            pagination : '.swiper-pagination',
          // some swiper options/callbacks
          // 所有的参数同 swiper 官方 api 参数
          // ...
        }
      }
    },
    mounted() {
      this.$http.get("/carousel").then((res)=>{
        let data = res.data;
        this.items=data;
      })  
    }
     
}

</script>

<style scoped>
*{
    margin: 0;
    padding: 0;
}
.swiper-container {
    width: 100%;
    height: 500px;
}

</style>



