<template>
    <div class="all">
        <div class="title">{{items.chargeTypeName}}</div>
        <div class="question">{{items.question}}</div>
        <div class="answer">{{items.answer}}</div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            items:{}
        }
    },
    mounted() {
        this.$http.get('/getOneQuestionById?id='+this.$route.params.id).then((res)=>{
            let data = res.data;
            this.items = data;
        })
    },
}
</script>
<style scoped>
.all{
    color: #666666;
    margin-top: 15px;
}
.title{
    font-size: 20px;
    margin-bottom: 20px;
}
.question{
    margin: 20px 0;
}
</style>


