<template>
    <div class="law-container">
        <div class="name">专注刑事案件15年，我们更擅长</div>
        <div class="logo">
            <ul>
                <router-link v-for="(item,index) in items" :key="index" tag="li" :to="`/penallaw/lawser/icondet/`+item.id ">
                    <img style="width:90px;height:90px" :src="item.icon" alt="" /><p>{{item.title}}</p></router-link>
            </ul>
        </div>

        <div class="word">
            <p class="wordOne" v-html="intro"></p>
           
        </div>
        <div style="font-size:26px">罪名分类</div>
        <div class="three">
            <div class="crime" v-for="(item,index) in ques" :key="index">
                <div class="title">{{item.chargeTypeName}}</div>
                <div v-for="(it,index) in item.questions" :key="index"><router-link tag="div" :to="`/penallaw/lawser/sortdet/`+it.id" class="sort">{{it.question}}</router-link></div>
                <!-- <div class="sort">最高刑期多少</div>
                <div class="sort">......</div> -->
            </div>
            <!-- <div class="crime">
                <div class="title">公司企业人员类犯罪预防与辩护</div>
                <div class="sort">重量、人数如何计算</div>
                <div class="sort">最高刑期多少</div>
                <div class="sort">......</div>
            </div>
            <div class="crime">
                <div class="title">时下热点类犯罪辩护</div>
                <div class="sort">重量、人数如何计算</div>
                <div class="sort">最高刑期多少</div>
                <div class="sort">......</div>
            </div>  -->
        </div>
        <div class="name">近期成功的案例</div>
        <div class="example">
            <div class="nav">
                <span v-for="(item,index) in sort" :key="index" :style="{background:firstIndex==index?'#666666':null,color:firstIndex==index?'white':null}"  @click="toggleClick(index)">{{item.charTypeName}}</span>
            </div>
            <div class="mid" >
                <span v-for="(item,index) in cases" :key="index" :style="{background:secondIndex==index?'#666666':null,color:secondIndex==index?'white':null}"  @click="toggleChargeClick(index)">{{item.chargeName}}</span>
            </div>
            <div class="case">
                <ul>
                    <router-link v-for="(item,index) in exams" :key="index" tag="li" :to="`/penallaw/lawser/exam/`+item.id">
                        <div class="data">
                            <div class="data-container">
                                <div class="month">{{item.publishDate.substr(5,5)}}</div>
                                <div class="year">{{item.publishDate.substr(0,5)}}</div>
                            </div>
                        </div> 
                        <div class="desc">
                            <div class="top">{{item.title}}</div>
                            <div class="foot">{{item.desc}}</div>
                        </div>
                        <div class="img">
                            <img :src="item.picUrl" alt="">
                        </div>
                    </router-link>
                   
                </ul>
            </div>
        </div>

    </div>
</template>

<script>
export default {
    name:'Lawser',
    data() {
        return {
            items:{},
            intro:{},
            ques:[],
            sort:[],
            cases:[],
            exams:[],
            container:[],
            year:[],
            date:[],
            firstIndex:'',
            secondIndex:'',
            host:this.GLOBAL.hostli
        }
    },
    methods: {
        toggleClick(index){      
                this.cases = this.sort[index].chargeNamesAndCases
                this.exams = this.cases[0].cases
                this.firstIndex=index;
        },
        toggleChargeClick (index) {
            this.exams = this.cases[index].cases
            this.secondIndex = index
        }
    },
    mounted() {
        this.$http.get("/listAllAdvantageDate").then((res)=>{
            let data = res.data;
            this.items = data;
        })
         this.$http.get("/listDefenseDesc").then((res)=>{
            let data = res.data[0].desc;
            this.intro = data
              
        })
        this.$http.get('/getQuestionDataSortByChargeType').then((res)=>{
            let data = res.data;
            this.ques = data;
        })
        this.$http.get('/getLinkageCaseData').then((res)=>{
            let data = res.data;
            this.sort = data;
    
            // for(var i =0;i<data.length;i++){
            //     this.container.push(this.sort[i].chargeNamesAndCases)          
            // }
           
        //    for(var i = 0;i<data[0].chargeNamesAndCases.length;i++){
        //        this.cases.push(this.sort[0].chargeNamesAndCases[i].chargeName)
        //    }

            this.cases = this.sort[0].chargeNamesAndCases

            this.exams = this.cases[0].cases
            
        })
        
    },
}
</script>

<style>
p{
    line-height: 30px;
}
.law-container{
    margin-top: 20px;
}   
.name{
    font-family: "Microsoft YaHei";
    font-size: 28px;
    color:#666666;
    text-align: center;
    margin-bottom: 50px;

}
.law-container ul{
    width: 100%;
    height: 350px;
}
.law-container ul li{
    list-style: none;
    float: left;
    margin-bottom: 50px;
    margin-right: 120px;
    cursor: pointer;
}
.logo ul li p{
    text-align: center;
}
.three{
    display: flex;
    width: 100%;
    height: 300px;
    margin: 30px 0;
    text-align: center;
    
}
.crime{
    flex: 1;
    border: 1px solid #ddd;
    text-align: center;
    height:100%;
    margin: auto 20px;

}
.crime .title{
    margin-top: 30px;
    font-size: 16px;
    font-weight:bold; 
}

.crime .sort{
    margin: 20px auto;
    color:#666666;
    cursor: pointer;
}
.example{
    width: 100%;
    height: 100%;
  
}
.example .nav{
    width: 100%;
    height: 60px;
    background: #f5f5f5;
    display: flex;
}
.example .nav span{
   flex: 1;
  line-height:60px;
   height: 100%;
   text-align: center; 
   cursor: pointer;  
}
.nav span:hover{
    background: #666666;
    color: white;
}

.mid{
    margin-top: 50px;
    width: 100%;
    height: 50px;
    display: flex;
}
.mid span{
    flex: 1;
    height: 100%;
    line-height: 50px;
    text-align: center;
    border: 2px solid #f5f5f5;
    margin: 0 10px;
    cursor: pointer;
}
.case{
    width: 100%;
    height: 100%;
}
.case ul{
    padding: 0;
    margin-top: 50px;
    
}
.case ul li{
    display: flex;
    width: 100%;
    height: 170px;
    margin: 25px 0;
}
ul li .data{
    height: 100%;
    flex: 1;

}
ul li .data .data-container{
    float: right;
    margin: 20px;
}
ul li .month{
    display: block;
    font-family:  "Microsoft YaHei";
    font-size: 25px;
    font-weight: bold;
   
}
ul li .year{
    color:#666666;
    font-size: 10px;

}
.top{
  margin: 20px 0;
  font-size: 20px;  
}
.foot{
    font-size: 14px;
    color: #666666;
}
ul li .desc{
    flex: 3;
    margin-right: 10px;
}

ul li .img{
    flex: 1;
}
.word{
    margin-top: 30px;
}
.wordOne{
    font-family: "Microsoft YaHei";
    font-size: 16px;
    color: #666666;
    margin:30px 0;
}
.wordTwo{
    font-family: "Microsoft YaHei";
    font-size: 16px;
    color: #666666;
    margin-left: 2em;
}
.wordThree{
    font-family: "Microsoft YaHei";
    font-size: 16px;
    color: #666666;
    margin-bottom: 60px;
}

</style>



