                <ul class="moveIt" style="position:absolute">
                    <li><router-link to="/labour/reply/1" tag="span">诉讼与仲裁应对</router-link></li>
                    <li><router-link to="/labour/design" tag="span">劳动用工制度事先设计</router-link></li>
                  <li><router-link to="/labour/select" tag="span">企业文化与策略选择</router-link></li>
                </ul>
            </li> -->
            <span class="headerSearch" v-show="isShow">
                <img style="width:18px;height:18px" src="../../assets/icon/kc_sea_ico11.png"
                alt=""
                @click="onHandleClick">
            </span>
            <span class="headerSearch" v-show="!isShow">
                <img style="width:18px;height:18px" src="../../assets/icon/kc_sea_ico21.png"
                alt=""
                @click="onHandleClickT"
                >
            </span>
          </ul>
          </div>
        </div>
          <div class="inputSearch" v-show="!isShow">
            <div>
             <span>
