</template>

<script>

export default {
    data(){
        return {
            items:[],
            sitems:[],
            isShow:true,
            // isMouseOver: false,
            // isMouseOverserver:false,
            // isMouseOverP:false,
            // isMouseOverlab:false
        }
    },
    methods:{
        limouseover(index){
            return [
            {
              [`limouseover`]: this.$data.items[index] && this.$data.items[index]._isHover
            }
          ]
        },
        onHandleClick(){
            this.isShow = false
        },
        onHandleClickT(){
            this.isShow = true
        },
        // handleOver () {
        //     this.isMouseOver = true


        // },
        handleMouseOver(index) {
            if (this.$data.items[index]._isHover) {
                return
            }
            this.$set(this.$data.items[index], '_isHover', true);
        },
        handleMouseLeave(index){
             if (this.$data.items[index].isHover) {
                return
            }
            this.$set(this.$data.items[index], '_isHover', false);
        }
        // handleOverserver() {
        //     this.isMouseOverserver=true
        // },
        // ins() {
        //     alert('yep')
        // },
        // handleOverPe() {
        //     this.isMouseOverP=true
        // },
        // handleOverLab() {
        //     this.isMouseOverlab=true
        // }
    },
    created() {
        this.$http.get('/firstMenu').then((res)=>{
            let data = res.data;
            this.items=data;
           
           
            
        })
    }
}
</script>

<style scoped>

* {
    padding: 0;
    margin: 0;
}
.headerSearch{
    float: right;
    margin-left: 10px;
    padding-top: 5px;
}
.header{
    width: 1200px;
    height: 115px;
    margin: 0 auto;
    text-align: center;
    vertical-align: middle;
}
.inputSearch{
    width:100%;
    position: absolute;
    z-index: 2;
    top: 110px;
    height: 45px;
    margin: 0 auto;
    background: rgba(72,73,75,0.5);
    left: 0px;
}
.inputSearch div{
    margin-left: 800px;
    position: relative;
    padding-top:6px;
}
.search{
    height: 32px;
    width: 79px;
    position: relative;
    border: none;
    color: #fff;
    background: #bc9244;
    float: left;
    bottom: 1px;
}
.inputS{
    height: 30px;
    float: left;
    width: 200px;
    border: 0px;
    padding-left: 5px
}
input::-webkit-input-placeholder{
    color: #bc9244;
}
.ula{
    float: right;
    margin-bottom:12px; 
    height: 30px;
    margin-top: 35px;
    
}
.ula ul li{
    padding-left: 15px;
}

ul {
    list-style: none
}
ul li {
    cursor: pointer;
    
}

.one{
    position: relative;
    float: left;
    color: black;
    font-size: 18px;
    font-family:"Microsoft YaHei";
    padding: 3px 6px;
    
}
.one ul {
    display: none;
    position: absolute;
    left:0px;
    top: 36px;
    z-index: 10;
    width: 400px;
    
}
.one li{
    float: left;
    font-size: 16px;
    font-family:"Microsoft YaHei";
    color: black;
    padding: 10px 6px;
}
.imgLogo{
    display: inline-block;
    margin-top: 28px;
    float: left;
}
.limouseover {
    border-bottom: 2px solid #b1976b;
}

ul li:hover ul{
    display: block;
            
}
ul li:hover .moveIt{
    display: block;
    position: absolute;
    left: -327px;
    }
/* .move{
    position: absolute;
    top: 2200px;
} */


/* ul li ul{
            position: absolute;
            padding-left:0px; 
            display: none
}

} */

</style>



