<template>
    <div>
        <div class="imgs">
           <img src="../../assets/icon/003.jpg" alt="" srcset="" style="height:300px;width:100%">
        </div>
        <div class="container">
            <div class="sidebar">
                <ul>
                    <router-link
                   tag="li"
                   to="/labour/server"
                   :style="{background:this.$route.path == '/labour/server'?'#4d4d4d':'',color:this.$route.path == '/labour/server'?'#fff':''}" 
                    >劳动法律服务
                    </router-link>
                    <router-link 
                    tag='li' 
                    to="/labour/reply"
                    class="seMenu"
                    :style="{background:this.$route.path == '/labour/reply'?'#4d4d4d':'',color:this.$route.path == '/labour/reply'?'#fff':''}" 
                    > 诉讼与仲裁应对
                    </router-link>
                    <router-link 
                    tag='li' 
                    to='/labour/design'
                    :style="{background:this.$route.path == '/labour/design'?'#4d4d4d':'',color:this.$route.path == '/labour/design'?'#fff':''}"  
                    class="seMenu"
                    >劳动用工制度事先设计
                    </router-link>
                    <router-link 
                    tag="li" 
                    to="/labour/select"
                    class="seMenu"
                    :style="{background:this.$route.path == '/labour/select'?'#4d4d4d':'',color:this.$route.path == '/labour/select'?'#fff':''}"  
                    >企业文化与策略选择
                    </router-link>         
                </ul>
            </div>
            <div class="content">
                <router-view></router-view>
            </div>
        </div>
        <Footer></Footer>
    </div>
</template>

<script>
import LabourTop from './components/LabourTop'
import Reply from './components/Reply'
import Design from './components/Design'
import Select from './components/Select'
import Footer from '../../common/Footer/footer'

export default {
    components:{
        LabourTop,
        Reply,
        Design,
        Select,
        Footer  
    }
}
</script>

<style scoped>
.imgs{
    background-color:#eee;
    width: 100%;
    height: 300px;
}
.container{
    width: 1200px;
     margin: 20px auto;
    display: flex;
    padding-top: 20px
}
ul{
    padding-left:0px; 
}
.seMenu{
    font-size: 16px;
    font-family: "Microsoft YaHei"
}
.sidebar{
    width: 280px;
    cursor: pointer;
}
.content {
    flex: 1; 
    margin-left: 50px;
    margin-top:20px 
}

.sidebar li{
    list-style: none;
    padding: 10px;
    text-align: center;
    background-color: #eee;
}
.sidebar li:hover{
    background-color: #4d4d4d;
    color: #eee;
}
</style>



