<template>
    <div class="collection">
        <ul>
            <li :class="{white:true,gray:items.indexOf(item)%2==0}" v-for="(item,index) in items" :key="index">
                <span class="title">
                    {{item.articleTitle}}
                </span>
                <div class="desc">
                    {{item.intro|introFilter}}
                </div>
                <router-link id="btn" tag="button" :to="`/introduce/Collection/form/items/`+item.articleId" >
                 查看详情
                </router-link>
            </li>
            <div style="margin:0 auto;width:400px">
             <div class="block" v-show="isShow" >
                <el-pagination
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-size="PageSize"
                layout="total, prev, pager, next"
                :total="totalCount">
                </el-pagination>
             </div>
            </div>
        </ul>
        
    </div>
    
</template>

<script>
export default {
    data(){
        return{
            items:[],
            isShow:true,
             // 默认显示第几页
           currentPage:1,
           // 总条数，根据接口获取数据长度(注意：这里不能为空)
           totalCount:1,
           // 默认每页显示的条数（可修改）
           PageSize:10
           

        }
    },
    filters:{
        introFilter(item){
            if(item.length>100){
                return item.slice(0,100)+'...'
            }else {
                return item
            }
        }
    },
    methods:{
        getData(){
            //当前页数currentPage+ 总条数totalCount+每页总数pagesize
            this.$http.get("/criminalLawArticlePageData?pageSize=10&pageNum="+this.currentPage).then((res)=>{
                let data = res.data.data;
                this.items =data;
                this.totalCount=res.data.count;
                this.isShow=true;
                // console.log(res)


            })
        },
        // 分页
         // 显示第几页
       handleCurrentChange(val) {
           // 改变默认的页数
           this.currentPage=val
          

           this.getData()


       },
    },
    created() {
       this.isShow=false;
    },
    mounted:function(){
            this.getData() 
    }
  }


</script>

<style scoped>
*{
    margin: 0;
    padding: 0;
}
.gray{
    height: 120px;
    background-color: #f5f5f5;
}
.white{
    height: 120px;
}
ul li{
    padding: 20px;
    list-style: none;

}
ul li .title{
    display: block;
    margin-bottom: 20px;
    font-family: "Microsoft YaHei";
    font-size: 16px;
    color: #707070;

}
ul li .title:hover{
    color: #333333;
}
ul li .desc{
    font-family: "Microsoft YaHei";
    font-size: 14px;
    color: #707070;
    width: 100%;
    height: 50px;
}
ul li .desc:hover{
    color: #333333;
}

#btn{
    float: right;
    padding: 5px 10px;
    font-family: "Microsoft YaHei";
    font-size: 12px;
    display: block;
    border:1px solid #b2976c;
    color: #b2972c;
    background-color: #f5f5f5;
    margin-bottom: -10px;
}
#btn:hover{
    background-color: #b2976c;
    color: white;
    cursor: pointer;
}

</style>
<style >
.el-pager li.active {
    color:#b1976b;
}
</style>



