import Vue from 'vue'
import Router from 'vue-router'
import Indexa from '@/pages/Index/Index'
import Introduce from '@/pages/Introduce/Introduce'
import Leader from '@/pages/Introduce/components/leader' 
import Office from '@/pages/Introduce/components/Office'
import Server from '@/pages/server/Server'
import ServerTop from '@/pages/server/components/ServerTop'
import EquityRightDesign from '@/pages/server/components/EquityRightDesign'
import Equityallocationtable from '@/pages/server/components/Equityallocationtable'
import Equityincentive from '@/pages/server/components/Equityincentive'
import Collection from '@/pages/Introduce/components/Collection'
import DetailCol from '@/pages/Introduce/components/DetailCol'
import Company from '@/pages/Introduce/components/Company'
import Detail from '@/pages/Introduce/components/Detail'
import CompanyDet from  '@/pages/Introduce/components/CompanyDet'
import LauorDet from  '@/pages/Introduce/components/LauorDet'
import FormDet from  '@/pages/Introduce/components/FormDet'
import PenalLaw from '@/pages/PenalLaw/PenalLaw'
import Crimedel from '@/pages/PenalLaw/components/Crimedel'
import Curdefense from '@/pages/PenalLaw/components/Currentdefense'
import Penaldef from '@/pages/PenalLaw/components/Penaldef'
import Crimedetail from '@/pages/PenalLaw/components/Crimedetail'
import Lawser from '@/pages/PenalLaw/components/Lawser'
import IconDet from '@/pages/PenalLaw/components/IconDet'
import SortDet from '@/pages/PenalLaw/components/SortDet'
import Example from '@/pages/PenalLaw/components/Example'
import LabourTop from '@/pages/Labour/components/LabourTop'
import Labour from '@/pages/Labour/Labour'
import Design from '@/pages/Labour/components/Design'
import Reply from '@/pages/Labour/components/Reply'
import Select from '@/pages/Labour/components/Select'
import LauorColl from '@/pages/Introduce/components/LauorColl'
import FormColl from '@/pages/Introduce/components/FormColl'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Indexa
    },
    {
      path:'/labour',
      name:'labour',
      component:Labour,
      redirect:'/labour/server',
      children:[
        {
          path:'/labour/server',
          name:'labourtop',
          component:LabourTop
        },
        {
          path:'/labour/design',
          name:'design',
          component:Design
        },{
          path:'/labour/reply',
          name:'reply',
          component:Reply
        },{
          path:'/labour/select',
          name:'select',
          component:Select
        },
      ]
    },
    {
      path:'/penallaw',
      name:'penallaw',
      component: PenalLaw,
      redirect:'/penallaw/lawser/1',
      children:[
        {
          path:'/penallaw/penaldef',
          name:'penaldef',
          component:Penaldef
        },{
          path:'/penallaw/lawser/:id',
          name:'lawser',
          component:Lawser
        },
        {
          path:'/penallaw/lawser/icondet/:id',
          name:'icondet',
          component:IconDet
        },
        {
          path:'/penallaw/lawser/sortdet/:id',
          name:'sortdet',
          component:SortDet
        },
        {
          path:'/penallaw/lawser/exam/:id',
          name:'example',
          component:Example
        },
        {
          path:'/penallaw/penaldef/:id',
          name:"penaldef",
          component:Penaldef
        },
        {
          path:'/penallaw/crimedel',
          name:'crimedel',
          component:Crimedel
        },
        {
          path:'/penallaw/currentdenfense',
          name:'currentdenfense',
          component:Curdefense
        },
        {
          path:'/penallaw/crimedetail',
          name:'crimedetail',
          component:Crimedetail
        }
      ]
    },
    {
      path: '/server',
      name: 'Server',
      component: Server,
      redirect:'/server/top',
      children:[
        {
          path:'/server/top',
          name:'servertop',
          component:ServerTop
        },
        {
          path:'/server/equityeightesign/:id',
          name:'equityeightesign',
          component:EquityRightDesign
        },{
          path:'/server/equityallocationtable',
          name:'Equityallocationtable',
          component:Equityallocationtable
        },{
          path:'/server/equityincentive',
          name:'Equityincentive',
          component:Equityincentive
        }
      ]
    },
    {
      path:'/introduce',  
      name:'Introduce',
      component: Introduce,
      redirect:'/introduce/office',
      children: [
        {
          path:'/introduce/office',
          name:'office',
          component:Office
        },
        {
          path:'/introduce/Collection/labour',
          name:'labours',
          component:LauorColl
        },
        {
          path:'/introduce/Collection/formlawyer',
          name:'formlawyer',
          component:FormColl
        },
        {
          path:'/introduce/Collection/items/:id',
          name:'DetailCol',
          component:DetailCol
          },
          {
            path:'/introduce/collection/company',
            name:'Company',
            component:Company
          },
          {
            path:'/introduce/Collection/company/items/:id',
            name:"CompanyDet",
            component:CompanyDet
          },
          {
            path:'/introduce/Collection/lauor/items/:id',
            name:'LauorDet',
            component:LauorDet
          },
          {
            path:'/introduce/Collection/form/items/:id',
            name:'FormDet',
            component:FormDet
          },
          {
            path:'/introduce/leader/:id',
            name:'detail',
            component:Detail
          },
        {
          path: '/introduce/collection/:id',
          name: 'Collection',
          component: Collection
        },
        {
          path: '/introduce/leader',
          name: 'Leader',
          component: Leader
        }
      ]
    }
  ]
})



