<template>
  <div id="container">
    <div class="title">{{items.articleTitle}}</div>
    <div class="intro">
      <span>作者名字:{{items.author}}</span>
      <span>{{items.editDate}}</span>
      <span>浏览:{{items.count}}</span>
    </div>
    <div class="desc" v-html="items.content"></div>
    <div class="footer">
      <span ref="pre" class="pre" @click="preClick">&lt;&lt;上一篇</span>
      <span ref="next" class="next" @click="nextClick">&gt;&gt;下一篇</span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: {},
      arr:[]
    };
  },
  methods: {
    preClick() {
      var arr = this.arr;
      // console.log(arr.indexOf(Number(this.$route.params.id)));
      
      for(var i=0;i<arr.length;i++){ 
         if(arr.indexOf(Number(this.$route.params.id))!=0){
         if(arr[i] == this.$route.params.id){
             this.$router.push("/introduce/Collection/form/items/" + arr[i-1]);
             break;
        }
      }
      else{
         
          return
      }
      }
    },
    nextClick() {
      var arr = this.arr;
    //   console.log(arr)
      for(var i=0;i<arr.length;i++){
         if(arr.indexOf(Number(this.$route.params.id))!=arr.length-1){
         if(arr[i] == this.$route.params.id){
             this.$router.push("/introduce/Collection/form/items/"+arr[i+1]);
             break;
        }
         }
         else{
           
             return

         } 
      }
      },
    fetchDate() {
      this.$http
        .get(
          "/criminalTypeDetail?id=" + this.$route.params.id
        )
        .then(res => {
          let data = res.data.articleDetail;
          this.items = data;
          if(this.$route.params.id==this.arr[0]){
          this.$refs.pre.style.color='#A9A9A9';       
          }
          
          else if(this.$route.params.id==this.arr[this.arr.length-1]){
              this.$refs.next.style.color='#A9A9A9';    
          }
          else{
              this.$refs.pre.style.color='#666666'; 
              this.$refs.next.style.color='#666666';    
          }
        });
    }
  },
  mounted() {
    this.$http
      .get(
        "/criminalTypeDetail?id=" + this.$route.params.id
      )
      .then(res => {
        let data = res.data.articleDetail;
        this.items = data;
        this.arr = res.data.idArr;
        if(this.$route.params.id==this.arr[0]){
            this.$refs.pre.style.color='#A9A9A9';     
        }
        else{
            this.$refs.pre.style.color='#666666'; 
            if(this.$route.params.id==this.arr[this.arr.length-1]){
            this.$refs.next.style.color='#A9A9A9';     
        }
        else{
             this.$refs.pre.style.color='#666666'; 
        }
        }
        
      });
     
  },
  watch: {
    $route: "fetchDate"
  }
};
</script>

<style scoped>
.title {
  display: block;
  margin-bottom: 20px;
  font-family: "Microsoft YaHei";
  font-size: 26px;
  color: #707070;
}
.intro span {
  font-family: "Microsoft YaHei";
  font-size: 12px;
  margin-right: 20px;
  color: #707070;
}
.desc {
  margin-top: 20px;
}
.footer {
  width: 500px;
  height: 20px;
  margin: 70px auto 30px auto;
}
.pre {
  margin-right: 50px;
  cursor: pointer;
  color: #666666;
}
.next {
  cursor: pointer;
  color: #666666;
}
</style>



