<template>
    <div class="callus" >
        <div class="telo" style="margin-top:10px;">联系我们</div>
        <div class="link" style="margin:20 auto"></div>
            <div class="maps">
                        <Map></Map>
                        <div class="tel">
                            <div class="address">
                                 <p>公司地址:青岛市市北区黑龙江南路2号万科中心A栋5楼</p>
                                 <p>联系电话:13375557517</p>
                                 <p>18561616688</p>
                                 <!-- <p>联系人:xxxxx</p> -->
                                 <p>QQ:XXXXXX</p>
                                 <P>微信:XXXXXXX</P>
                                 <p>公众号:XXXXXXX</p>
                                 <p>邮编:266000</p>
                        </div>
                </div>
            </div>
    </div>
</template>

<script>
import Map from '../../../common/Map/Map'
export default {
    components:{
        Map
    },
    name:'CallUs',
}
</script>

<style scoped>
.callus{
    margin-bottom: 20px;
    padding: 0px;
}
.map{
    height: 310px;
    width: 700px;
}
.address{
    width: 365px;
    height: 285px;
    text-align: center;
    margin: 5px auto;
    background: rgba(12,92,110,0.5);
     padding-top: 8px;
}
.address p{
    font-size: 16px;
    color: #fff;
    font-family: "Microsoft YaHei";
    margin: 10px auto;
}
.telo{
    font-size: 28px;
    font-family: "Microsoft YaHei";
    margin: 0 auto;
    width: 120px;
    margin-top: 10px;
}
.tel{
    flex: 1;
    height: 310px;
    width: 500px;
    background-image: url(../../../assets/icon/contact_bj.jpg);
    background-repeat: none;
    background-size: 100%;
}
.maps{
    display: flex;
    width: 1200px;
    margin: 0 auto;
    margin-bottom: 5px;
}
.link{
    border-bottom: 2px solid #bc9244;
    width: 3%;
    margin:30px auto 
}
</style>



