<template>
    <div class="law-container">
        {{items}}
    </div>
</template>
<script>
export default {
    data() {
        return {
           items:{} 
        }
    },
    mounted(){
        this.$http.get("/getOneDefenseAdvantageById?id="+this.$route.params.id).then((res)=>{
            let data = res.data.content;
            this.items=data;
        })
    }
}
</script>
<style  scoped>

</style>>
    


